.homeCards__ {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-top: 50px;

    &card {
        background-color: rgb(202, 202, 202);
        border-radius: 10px;
        height: 50px;
        width: 180px;
        border-style: solid;
        border-width: 2px;
        display: flex;
        justify-content: center;
    }

    &card a {
        text-decoration: none;
        color: rgb(0, 0, 0);
        margin: auto;
    }

    &card a:hover {
        text-decoration: none;
        color: rgb(141, 141, 141);
    }
}